<template>
    <div id="countBox">
        <div class="white txal">count</div>
        <div id="myChart" class="" :style="{width: '100%', height: '350px'}"></div>
        <div id="myChart1" class="" :style="{width: '100%', height: '350px'}"></div>
    </div>
</template>

<script>
    // import echarts from 'echarts'
export default {
    data() {
        return {
            
        }
    },
    created() {
        this.getdata()
    },
    methods: {
        getdata(){
            this.$api.getcount({},res=>{
                console.log(res)
                let xdata=[],ydata=[]
                for(let i of res.data){
                    xdata.push(i.createTime.split(' ')[0])
                    ydata.push(i.number)
                }
                this.paintingBar(xdata,ydata)
                this.paintingLine(xdata,ydata)
            })
        },
        paintingBar(xdata,ydata){
            let myChart = echarts.init(document.getElementById('myChart'))
            let option={
                backgroundColor: "#38445E00",
                tooltip: {},
                grid: {
                    top: '24%',
                    left: '7%',
                    bottom: '6%',
                    containLabel: true
                },
                xAxis: {
                    data: xdata,
                    axisTick: {
                        show: false
                    },
                    axisLine: {
                        lineStyle: {
                            color: 'rgba(255, 129, 109, 0)',
                            // width: 1 //这里是为了突出显示加上的
                        }
                    },
                    axisLabel: {
                        rotate:-10,
                        textStyle: {
                            color: 'white',
                            fontSize: 10
                        }
                    }
                },
                yAxis: [{
                        show:false,
                    }
                ],
                series: [{
                    name: 'count',
                    type: 'pictorialBar',
                    // type: 'bar',
                    barCategoryGap: '-100%',
                    symbol: 'path://M0,10 L10,10 C5.5,10 5.5,5 5,0 C4.5,5 4.5,10 0,10 z',
                    label: {
                        show: false,
                        // position: 'top',
                        // distance: 15,
                        color: 'white',
            // 			fontWeight: 'bolder',
                        fontSize: 16,
                    },
                    itemStyle: {
                        normal: {
                            color: {
                                type: 'linear',
                                x: 0,
                                y: 0,
                                x2: 0,
                                y2: 1,
                                colorStops: [{
                                        offset: 0,
                                        color: 'rgba(232, 94, 106, .1)' //  0%  处的颜色
                                    },
                                    {
                                        offset: 1,
                                        color: 'red' //  100%  处的颜色
                                    }
                                ],
                                global: false //  缺省为  false
                            }
                        },
                        emphasis: {
                            opacity: 1
                        }
                    },
                    data: ydata,
                    z: 5
                }]
            };
            myChart.setOption(option);
        },
        paintingLine(xdata,ydata){
            let myChart = echarts.init(document.getElementById('myChart1'))
            let option={
                backgroundColor: "#020d2200",
                title: '',
                grid: {
                    top: '24%',
                    left: '7%',
                    bottom: '6%',
                    containLabel: true
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'none'
                    },
                },
                xAxis: [{
                        type: 'category',
                        position: "bottom",
                        data: xdata,
                        boundaryGap: true,
                        offset: 10,
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        },
                        axisLabel: {
                            textStyle: {
                                color: '#b6b5ab'
                            }
                        }
                    }

                ],
                yAxis: [{
                    show: false,
                }],
                color: ['#e54035'],
                series: [
                    {
                        symbol: 'path://d="M512 128c35.328 0 64-28.672 64-64 0-35.328-28.672-64-64-64C476.672 0 448 28.672 448 64 448 99.328 476.672 128 512 128zM192 768c-35.328 0-64 28.672-64 64 0 35.328 28.672 64 64 64 35.328 0 64-28.672 64-64C256 796.672 227.328 768 192 768zM64 448C28.672 448 0 476.672 0 512c0 35.328 28.672 64 64 64 35.328 0 64-28.672 64-64C128 476.672 99.328 448 64 448zM192 256c35.328 0 64-28.672 64-64 0-35.328-28.672-64-64-64C156.672 128 128 156.672 128 192 128 227.328 156.672 256 192 256zM832 768c-35.328 0-64 28.672-64 64 0 35.328 28.672 64 64 64 35.328 0 64-28.672 64-64C896 796.672 867.328 768 832 768zM960 448c-35.328 0-64 28.672-64 64 0 35.328 28.672 64 64 64 35.328 0 64-28.672 64-64C1024 476.672 995.328 448 960 448zM512 256C370.624 256 256 370.624 256 512c0 141.376 114.624 256 256 256 141.376 0 256-114.624 256-256C768 370.624 653.376 256 512 256zM512 640c-70.72 0-128-57.28-128-128 0-70.72 57.28-128 128-128 70.72 0 128 57.28 128 128C640 582.72 582.72 640 512 640zM512 896c-35.328 0-64 28.672-64 64 0 35.328 28.672 64 64 64 35.328 0 64-28.672 64-64C576 924.672 547.328 896 512 896zM832 256c35.328 0 64-28.672 64-64 0-35.328-28.672-64-64-64-35.328 0-64 28.672-64 64C768 227.328 796.672 256 832 256z"',
                        symbolSize: 24,
                        name: "count",
                        type: "line",
                        yAxisIndex: 0,
                        data: ydata,
                        itemStyle: {
                            normal: {
                                borderWidth: 5,
                                color: {
                                    colorStops: [{
                                            offset: 0,
                                            color: 'red'
                                        },

                                        {
                                            offset: 1,
                                            color: '#000'
                                        }
                                    ],
                                }
                            }
                        }
                    }
                ]
            }
            myChart.setOption(option);
        }
    },
}
</script>

<style scope lang="less">
    #myChart{
      /* position: fixed !important;
      left: 0;
      z-index: -1;
      top: 0; */
    }
</style>


